.usercard-container {
  display: flex;
  flex-direction: column;
  border-radius: 15px;
  padding: 0px 20px 25px 20px;
  text-align: center;
  align-items: center;
  font-size: 1.6em;
  box-shadow: 3px 3px 10px grey;
  background-color: lightyellow;
}

.user-address {
  border-top: 1px solid gainsboro;
}
.usercard-container span {
  margin-right: 10px;
}
